
const Hero = ({ img, title, text }) => {
  return (
    <>
      <div className="flex gap-5 justify-center items-center">
        <img src={img} alt="" />
        <h3 className="text-lg font-semibold">{title}</h3>
      </div>
      <div>
        <p
          className={`blur-lg backdrop-sepia-0 rounded-xl text-right py-4 px-2 `} // Apply your CSS Modules class here
        >
          {text}
        </p>
      </div>
    </>
  );
};

export default Hero;
