import React from 'react'
import Hero1 from '../component/hero1/Hero1'
import firstHeroImg from '../imgs/Open Peeps - Bust 1.svg'
import Btn from '../component/btn/Btn'
const FirstPage = () => {
  return (
    <>
        <Hero1 img={firstHeroImg} title={'احمد ٢٠ سنة، طالب بالجامعة'} text={'صرف ١٤٠ الف البارحة واليوم بقت عندة ٥٠٠ دينار هوه عندة اهداف مالية يريد يحققهن وفكر كال لازم اتعلم طريقة الادارة المالية، كام يدور على اشياء بالادارة المالية ولكى تطبيق محفظتي'}/>
        <Btn link={'second'}/>
        
    </>
  )
}

export default FirstPage