import React from "react";
import {Link} from 'react-router-dom'
const Btn = ({link}) => {
  return (
    <div className="flex justify-center  ">
      <Link to={link} className="bg-blue-400 text-center w-full rounded-full text-white py-2 ">
        التالي
      </Link>
    </div>
  );
};

export default Btn;
