import React from 'react'
import Hero1 from '../component/hero1/Hero1'
import Btn from '../component/btn/Btn'
import secondHeroImg from '../imgs/Open Peeps - Bust-2 1.svg'
const SecondPage = () => {
  return (
    <>
        <Hero1 img={secondHeroImg} title={'هسه لازم نعرف شنو تثقيف مالي'} text={'التثقيف المالي : هو مجموعة من المهارات والمعارف والأدوات التي تمكننا من إدارة أموالنا بشكل أفضل. '}/>
        <Btn link={'/third'}/>
    </>
  )
}

export default SecondPage