import React from "react";
import footerImg1 from "../../imgs/Group 2179.svg";
import footerImg2 from "../../imgs/Group 2181.svg";
import footerImg3 from "../../imgs/Group 2263.svg";

const Footer = () => {
  return (
    <div className="flex justify-between items-center px-4 py-2 mt-10">
      <div>
        <img src={footerImg1} alt=""  className="hover:cursor-pointer"/>
      </div>
      <div>
        <img src={footerImg3} alt=""  className="hover:cursor-pointer"/>
      </div>
      <div>
        <img src={footerImg2} alt=""  className="hover:cursor-pointer"/>
      </div>
   
    </div>
  );
};

export default Footer;
